import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Loading from "../components/LoadingError/Loading";
import Toast from "../components/LoadingError/Toast";
import { login } from "../Redux/Actions/userActions";
import Message from "./../components/LoadingError/Error";
import Recapcha from "react-google-recaptcha";

const Login = ({ history }) => {
  window.scrollTo(0, 0);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const [recapchaValue, setrecapchaValue] = useState(null);

  const dispatch = useDispatch();

  const userLogin = useSelector((state) => state.userLogin);
  const { error, loading, userInfo } = userLogin;

  useEffect(() => {
    if (userInfo) {
      history.push("/");
    }
  }, [userInfo, history]);

  const submitHandler = (e) => {
    e.preventDefault();

    if (recapchaValue) dispatch(login(email, password));
  };
  return (
    <>
      <Toast />
      <div
        className="card shadow mx-auto"
        style={{ maxWidth: "380px", marginTop: "100px" }}
      >
        <div className="card-body">
          {error && <Message variant="alert-danger">{error}</Message>}
          {loading && <Loading />}
          <h4 className="card-title mb-4 text-center">Iniciar Sesin</h4>
          <form onSubmit={submitHandler}>
            <div className="mb-3">
              <input
                required
                className="form-control"
                placeholder="Email"
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </div>
            <div className="mb-3">
              <input
                required
                className="form-control"
                placeholder="Password"
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <div className="mb-4">
              <Recapcha
                onChange={(value) => setrecapchaValue(value)}
                sitekey="6LcaFQkpAAAAAHHM7wIUzgN19JRJAbIbRpu5GvhM"
              ></Recapcha>
            </div>
            <div className="mb-4">
              <button
                disabled={!recapchaValue}
                type="submit"
                className="btn btn-primary w-100"
              >
                Iniciar Sesin
              </button>
            </div>
          </form>
        </div>
      </div>
    </>
  );
};

export default Login;
